import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

function ResultBoard() {
    console.log(process.env.REACT_APP_API_URL);
    const dummyresults = [
        {
            date: "Jul 23, 2024",
            type: "result",
            name: "Regarding Admit Card of Exams",
        },
        {
            date: "Jul 20, 2024",
            type: "result",
            name: "Summer Vacations",
            highlight: true,
        },
        {
            date: "Jul 15, 2024",
            type: "Result",
            name: "Results of Term1 Examinations",
        },
        {
            date: "Jul 10, 2024",
            name: "Applications invited for the post of Teachers for the session 2023-24",
        },
        {
            date: "Jul 5, 2024",
            name: "Admission result for class I-VIII",
        },
    ];
    let [results, setresults] = useState(dummyresults);
    useEffect(() => {
        let fun = async () => {
            let response = await fetch(
                process.env.REACT_APP_API_URL + "/api/view-results"
            );
            console.log(response);
            let data = await response.json();
            console.log(data);
            setresults(data);
        };
        fun();
    }, []);

    return (
        <div className="container-fluid mt-sm-4 m-2 p-2">
            <h2>Exam Results</h2>
            <div className="list-group">
                {results.length === 0 ? (
                    <>{"No results are there currently"}</>
                ) : (
                    results.map((result, index) => (
                        // eslint-disable-next-line react/jsx-no-target-blank
                        <a
                            target="_blank"
                            key={index}
                            href={
                                result.link ||
                                `${
                                    process.env.REACT_APP_API_URL +
                                    "/view-results"
                                }`
                            }
                            className={`list-group-item list-group-item-action ${
                                result.highlight ? "active" : ""
                            }`}
                        >
                            {/* <div className="d-flex w-100 justify-content-between">
                                <small className="text-muted">
                                    {new Date(
                                        result.date
                                    ).toLocaleDateString() +
                                        " " +
                                        new Date(
                                            result.date
                                        ).toLocaleTimeString()}
                                </small>
                                {result.type && (
                                    <span className="badge bg-primary rounded-pill">
                                        {result.type}
                                    </span>
                                )}
                            </div> */}
                            <p className="mb-1 text-decoration-underline">
                                {result.className + " - " + result.examName}
                            </p>
                        </a>
                    ))
                )}
            </div>
        </div>
    );
}

export default ResultBoard;
